@keyframes rainbow {
  0% { background-color: #ff003c; }
  16% { background-color: #fffc00; }
  33% { background-color: #00ff5e; }
  50% { background-color: #00cfff; }
  66% { background-color: #2e00ff; }
  83% { background-color: #ff00c8; }
  100% { background-color: #ff003c; }
}
.rainbow-bg {
  animation: rainbow 1.5s linear 0s;
}

.jpeg-popup-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100vw; height: 100vh;
  background: rgba(0,0,0,0.22);
  z-index: 9998;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none; /* so the UI below is not blocked if you want */
}

.jpeg-popup-img {
  width: 220px;
  height: 220px;
  animation: spin3d 1.2s linear infinite;
  transform-style: preserve-3d;
  will-change: transform, opacity;
  z-index: 9999;
  pointer-events: none;
  filter: drop-shadow(0 0 20px #fff7);
}
